body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body, #root, .App {
  height: 100%;
  width: 100%;
}

.App {
  padding: 10px;
  font-size: 12px;
  box-sizing: border-box;
}

/**
 * Dropzone styling
 */

.TraceViewDropzone {
  background: red;
  display: flex;
  flex-grow: 1;
  flex-shrink: 1;

}

.isHighlighted {
  background-color: rgba(255, 234, 0, 0.54) !important;
}

.HighlightedNav {
  position: fixed;
  bottom: 10px;
  right: 2px;
}

.HighlightedNav button {
  padding: 5px;
  margin: 0 10px;
}

.TraceEntry {
	border-bottom: 1px solid darkgray;
	padding: 5px 0;
}

.TraceEntry-data {
	display: flex;
	justify-content: flex-end;
}


.TraceEntry-data div {
	display: inline-block;
	vertical-align: middle;
}

.TraceEntry div {
	box-sizing: border-box;
}

.TraceEntry-metric {
	width: 150px;
	text-align: right;
	white-space: nowrap;
	text-overflow: ellipsis;

}

.TraceEntry-args, .TraceEntry-return {
	white-space: nowrap;
	text-overflow: ellipsis;
	max-width: 300px;
	overflow: hidden;
}

.TraceEntry-args {
	color: green;
}

.TraceEntry-func {
	flex-grow: 1;
	justify-content: flex-start;
}

.TraceEntry-funcname {
	font-weight: bold;
}

.TraceEntry-return {
	color: goldenrod;
	font-style: italic;
	overflow: hidden;
	height: 100%;
}

.TraceEntry-children {
	border-left: 1px solid lightgray;
	margin-top: 2px;
}

.TraceEntry-children .TraceEntry:last-child {
	border-bottom: none;
}

.TraceEntry-children.collapsed, .TraceEntry-children-leftpad {
	cursor: pointer;
}

.TraceEntry-children.collapsed {
	height: 5px;
	background: darkgray;
	margin: 5px 0 0;
}

.TraceEntry-children {
	display: flex;
	justify-content: flex-start;
}

.TraceEntry-children-leftpad {
	width: 20px;
	flex-shrink: 0;
}

.TraceEntry-children-leftpad:hover {
	background: lightgray;
}

.TraceEntry-children-container {
	flex-grow: 1;
}

.TraceEntry-metric-percentage {
	color: darkgray;
}

